body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.entire-page {
    background-color: #4161c9;
    height: 100vh;
    display: flex;
}

.inputContainer {
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80vh;
    margin-top: 8%;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 1px 5px 0 rgba(0,0,0,0.12), 0 3px 1px -2px rgba(0,0,0,0.2);
    opacity: 0.9;
}

@media screen and (min-width: 770px) {
    .inputContainer {
        width: 50vh;
        margin-left: 38.3%;
    }
}

.signInButton {
    background-color: #E8CA29;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-items: center;
    height: auto;
    margin-top: auto;
    margin-left: 10%;
}

div.col.input-field.s10.m10.l10 {
    margin-bottom: -10%;
}
.newUser {
    background-color: transparent;
    box-shadow: 0px 0px 0px;
    font-size: 80%;
    color: black;
    text-transform: lowercase;
    display: flex;
    justify-content: center;
    letter-spacing: .5px;
    align-items: center;
    
}


.btn.signInButton:hover {
    background-color: #47525E;
    color: white;
}

.logo {
    font-size: 400%;
    margin-bottom: 1%;
    color: #A1CDF7;
    text-shadow: 4px 2px #D5E7F7;;
    margin-top: 1%;
    margin-right: 5%;
}
.logo {
    width: 150px;
    height: 150px;
    margin-top: -30%;
}

nav {
    background-color: #4161c9;
    display: flex;
    align-items: center;
    justify-content: center
}

.row.with-padding {
    padding-top: 3%;
    display: flex;
}

.main {
    display: flex;
}
.cardsContainer{
    display: flex;
    flex-wrap: wrap;
    /* margin: 0 auto; */
    justify-content: space-evenly;
    flex:1 1;
}

.pokemonCard {
    padding-bottom: 1em;
    margin-bottom: 1em;
    min-width: 30%;
    max-width: 30%;
    flex: 1 1;
}

.deckContainer {
    flex: 1 1;
    max-width: 30%;
    padding-left: 1em;
}
